/*--------------------------------------------------------------
# Hero Slider
--------------------------------------------------------------*/
.aarambha-kits-hero-slider-container .hero-slide-wrapper {
    position: relative;
    height: 500px;
}

.aarambha-kits-hero-slider-container .hero-slide-contents {
    position: relative;
    padding: 50px;
    left: 0;
    right: 0;
}

.aarambha-kits-hero-slider-container .hero-slide-wrapper::before {
    position: absolute;
    display: block;
    content: "";
    height: 100%;
    width: 100%;
    top: 0;
    background-color: thistle;
}

.aarambha-kits-hero-slider-container .hero-slide-contents .slide-image {
    margin-bottom: 25px;
}

.aarambha-kits-hero-slider-container .hero-slide-contents .slide-image img {
    width: 200px;
    height: 200px;
}

.aarambha-kits-hero-slider-container .hero-swiper-button-next,
.aarambha-kits-hero-slider-container .hero-swiper-button-prev {
    position: absolute;
    top: 50%;
    z-index: 10;
    cursor: pointer;
    background-size: 27px 44px;
    background: no-repeat 50%;
    font-size: 25px;
    height: 40px;
    width: 40px;
    line-height: 40px;
    text-align: center;
}

.aarambha-kits-hero-slider-container .hero-swiper-button-prev {
    left: 10px;
    right: auto;
}

.aarambha-kits-hero-slider-container .hero-swiper-button-next {
    right: 10px;
    left: auto;
}

.aarambha-kits-hero-slider-container .swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
    background: #000;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
    transform: scale(0);
    -webkit-transform-origin: left top;
    -ms-transform-origin: left top;
    transform-origin: left top;
}

.aarambha-kits-hero-slider-container .swiper-container-horizontal>.swiper-pagination-progressbar {
    width: 100%;
    height: 4px;
    left: 0;
    top: 0;
}

.slide-content .slide-title {
    font-size: 30px;
    color: #fff;
    margin-bottom: 15px;
}

.slide-content .slide-decription {
    color: #fff;
    margin-bottom: 20px
}

.slide-content .slide-button {
    background: #E4BD86;
    display: inline-block;
    padding: 8px 17px;
    color: #fff;
    cursor: pointer;
}

.swiper-slide-active .slide-content .slide-button:hover {
    background: #000;
    color: #fff;
}

.hero-swiper-button-prev,
.hero-swiper-button-next {
    color: #fff;
    background: #E4BD86;
}

.hero-swiper-button-prev:hover,
.hero-swiper-button-next:hover {
    color: #fff;
    background: #000;
}

.aarambha-kits-hero-slider-container .swiper-pagination {
    bottom: 0;
    left: 0;
    right: 0;
}

.aarambha-kits-hero-slider-container .swiper-pagination-bullets .swiper-pagination-bullet {
    background: #000;
    margin: 0 3px;
    width: 12px;
    height: 12px;
}

.hero-slide-contents .slide-content {
    width: 100%;
}